import { Indicator } from 'mint-ui';
import List from 'components/list/list'
import NoData from 'base/noData/noData'
import Scroll from 'base/scroll/scroll'

import { BannerList } from 'api/banner'
import { GoodsList } from 'api/goods'
import { ERR_CODE } from 'api/config'

export default {
	data() {
		return {
			list: [],
			topStatus: '',
			allLoaded: false,
			loading: true,
			bannerList: [],

			currentPage: -1,
			isData: false,
			// ...
		};
	},
	methods: {
		getBannerList() {
			BannerList().then((res) => {

				if(res.data.code === ERR_CODE) {
					this.bannerList = res.data.data
				} else {
					ShowToast(res.data.msg)
				}
			}).catch((res) => {

			})
		},
		bannerDetail(item) {
			console.log(item)
			this.$router.push({
				name: `detail`,
				query: {
					id: item.link
				}
			})
		},

		getGoodList() {
			if(this.isData) {
				return;
			}
			this.currentPage++;
			GoodsList(this.currentPage).then((res) => {
				if(res.data.code === ERR_CODE) {
					if(res.data.data === null) {
						this.isData = true;
						return;
					}
					this.list = this.list.concat(res.data.data);
				}
			})
		},
		goDetail(val) {
			this.$router.push({
				name: `detail`,
				query: {
					id: val.goods_id
				}
			})
		},
	},
	created() {
		this.getBannerList();
		this.getGoodList();
	},
	components: {
		List,
		NoData,
		Scroll
	},
};



